.social-icons a {
  display: inline-block;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff !important;
  border-radius: 100%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 3.5rem;
  margin-right: 1rem;
}

.social-icons a:last-child {
  margin-right: 0;
}

.social-icons a:hover {
  background-color: #0c1329;
}

.email{
  color:#0c1329;
  display: inline;
}

.email:hover{
  cursor: pointer;
  color: #354782;
}

.alertify-notifier .ajs-message{
  font-family: 'Muli', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  color: white;
  background-color:#0c1329;
  border-color: #0c1329;
}

.resume {
  position: absolute;
  padding-top: 20px;
  right: 110px;
  font-size: 18px;
  font-weight: lighter;
  color: #081345;
}

.resume a{
  text-decoration: none;
}

.resume:hover {
  cursor: pointer;
  color: #354782;
}
