.lang{
  display: inline-block;
  font-size: 17px;
  color: #0c1329;
}

.lang + .lang{
  display: inline-block;
  font-size: 17px;
  color: #0c1329;
  margin-left: 10px;
}

.lang:hover{
  text-decoration: underline;
}

.card i {
  color: #0c1329;
  font-size: 2.2em;
}

.mycardeck {
  margin-top: 20px;
}

.mycard {
  max-width: 350px;
}

.mycard:hover {
  cursor: default;
  border: solid 1px #0c1329;
  -moz-box-shadow: 5px 5px 0px #0c1329;
  -webkit-box-shadow: 5px 5px 0px #0c1329;
  box-shadow: 5px 5px 0px #0c1329;
}

.control {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-left: 10px;
}

.next {
  font-size: 3em;
  color:grey;
}

.next:hover {
  cursor: pointer;
  color: #0c1329;
}

.prev {
  margin-top: 10px;
  transform: rotate(180deg);
  font-size: 3em;
  color:grey;
}

.prev:hover {
  cursor: pointer;
  color: #0c1329;
}
